import React, { Component } from 'react';
import { connect } from 'react-redux';
import axios from 'axios';
import Swal from 'sweetalert2'

import config from "../server/config.json";
import {increment_item_to_shopping_cart} from "../store/actions/productsAction";
class Order extends Component {
    constructor(props)
    {
      super(props);
      this.addFormData = this.addFormData.bind(this);
    }
    state = { 
        
             sum:0,
            
   
    }
    
   componentDidUpdate(){
     console.log(this.props.$shoppingProducts)
   }
   addFormData(evt)
   {
     evt.preventDefault();
     const fd = new FormData();
     fd.append('fname', this.refs.fname.value);
     fd.append('lname', this.refs.lname.value);
     fd.append('address', this.refs.address.value);
     fd.append('totalprice', this.refs.totalprice.value);
     fd.append('identitycard', this.refs.identitycard.value);

     


     
     axios.post('http://localhost/Project_DB/addProduct.php', fd
     ).then(res=>
     {
     //Success alert
      Swal.fire({
     title: 'Therichpost',
     text: res.data.data,
     type: 'success',
     
   });
   this.myFormRef.reset();
   }
   );
   }

   
   
   
   
   render() { 
     return ( <div className="ord">
 
       <ul class="list-group">My Items
       <div className="col-2">
     
       {this.props.$shoppingProducts.map((product) => (
             <li className="list-group-item">Name: {product.ProductName}  &nbsp;
         
            
         {/* <button onClick={this.DecreaseItem}>-</button>  */}
         &nbsp;
         
         <button className="btn btn-success" onClick={()=>this.setState({sum:this.state.sum + (parseInt(product.Price)*parseInt(product.count))})} >Add to order </button>    
          
         </li>
           
           ))}
            
 
 
 
    
   
        
           </div>
           </ul>  
           <br/>
           <br/>
          
           <form ref={(el) => this.myFormRef = el}>
        <div className="form-group">
        <input type="text" className="form-control" id="First Name" aria-describedby="emailHelp" placeholder="Enter First Name" ref="fname" />
        
        </div>
        <div className="form-group">
        <input type="text" className="form-control" id="Last Name" placeholder="Enter Last Name" ref="lname" />

        </div>
        <div className="form-group">
        <input type="text" className="form-control" id="address" placeholder="Enter Address" ref="address" />
        
        </div>
        <div className="form-group">
        <input type="text" className="form-control" id="identitycard" placeholder="Enter you id card"  ref="identitycard" />
        
        </div>
        <div className="form-group">
       <span className="price">Need to pay</span>: <input type="text"  id="totalprice" value={this.state.sum}  ref="totalprice" />
        
        </div>
        <button type="submit" className="btn btn-primary"  id="btnord" onClick={this.addFormData}>Submit</button>
      </form>

         


         

          
     </div> );
   }
 }
 
 
 const mapStateToProps = (state) => {
   return {
     $shoppingProducts : state.productsReducer.shopppingCartItems
   }
 }
 

 
 export default connect(mapStateToProps)(Order)  ;