import React, { Component } from 'react';
import axios from "axios";
import config from '../server/config.json';
import { connect } from 'react-redux';
import {increment_item_to_shopping_cart} from "../store/actions/productsAction";
import { NavLink } from 'react-router-dom';
class Cart extends Component {
  state = { 
     
   
   }
   
  componentDidUpdate(){
    console.log(this.props.$shoppingProducts)
  }
 
  ToggleClick = () => {
    this.setState({ show: !this.state.show });
  }
  
  
  render() { 
    return ( <div className="cart">

      <ul class="list-group">Cart Items
      <div className="col-2">
    
      {this.props.$shoppingProducts.map((product) => (
            <li className="list-group-item">{product.ProductName}  &nbsp;
            {product.Price}$ &nbsp;
            <button className="btn btn-info" onClick={() => this.props.$incrementItem(product.ID) }>+</button>
        {/* <button onClick={this.DecreaseItem}>-</button>  */}
        &nbsp;
        <br/>
        <button className="btn btn-info" onClick={this.ToggleClick}>
          { this.state.show ? 'Hide quantity' : 'Show quantity' }
        </button>
        <br/>
        { this.state.show ? <h2>{ product.count }</h2> : '' }
         
        </li>
          
          ))}



   
  
       
          </div>
          </ul>  
          <NavLink className="btn btn-primary" to={"/Order"} >Order now</NavLink>
          
         
          
    </div> );
  }
}


const mapStateToProps = (state) => {
  return {
    $shoppingProducts : state.productsReducer.shopppingCartItems
  }
}

const mapDispatchToProps = dispatch => {
    return {
    //  $addItemToCart: (product) => dispatch(add_item_to_shopping_cart(product))
        $incrementItem: (id) => dispatch(increment_item_to_shopping_cart(id))
    }
}
export default connect(mapStateToProps, mapDispatchToProps)(Cart)  ;