
const initialState = {
    products : [],
    shopppingCartItems: [],
    Orderdetails :[],
    
};

const productsReducer = (state = initialState , action ) => {

    switch (action.type) {
        case 'ADD_ITEM_TO_SHOPPING_CART':
            console.log(action.product)
            return {
                
               ...state,
               shopppingCartItems : [ ...state.shopppingCartItems, {...action.product, count: 1}] ,
                
            }
            case 'INCREMENT_ITEM':
                    const index = state.shopppingCartItems.findIndex(x => x.ID == action.id)
                    const newshopppingCartItems = [...state.shopppingCartItems];
                    newshopppingCartItems[index].count++;
                    return {
                       ...state,
                       shopppingCartItems : newshopppingCartItems ,

                       
                    }
                    
                
                
    
        default:
           return state
    }
}

export default productsReducer;