import React, { Component } from 'react';
import axios from 'axios';
import config from "../server/config.json";
import { Link, NavLink } from "react-router-dom";
import { Button } from 'react-bootstrap';
import { connect } from 'react-redux';
import { add_item_to_shopping_cart } from '../store/actions/productsAction';





class Products extends Component {
  state = {
    prod: [],
    isLoaded: true,
    id: 0,
  };
  async componentDidMount() {
    const { data: prods } = await axios.get(
      config.prod + "?id=" + this.props.match.params.id
    );
    this.setState({ prod: prods });
    this.setState({ isLoaded: true });
    // console.log(prod);
  }
  async componentDidUpdate(prevProps) {
    if (prevProps.match.params.id !== this.props.match.params.id) {
      const { data: prods } = await axios.get(
        config.prod + "?id=" + this.props.match.params.id
      );
      this.setState({ prod: prods });

      this.setState({ isLoaded: true });
    }
  }

  render() {
    return (
      <div className="prd">
        {/* {this.state.isLoaded &&
              this.state.prod.map((prods) => (
                <div class="card" key={this.props.match.params.id}> 
                  <img class="img" src="img_avatar.png" alt="Avatar"/>

                <div class="container">
                {prods.ProductName} &nbsp;
                {prods.Price}
                </div>
                </div>
              ))} */}

        {/* id:{this.props.match.params.id} */}
        <div className="row">
          {this.state.isLoaded &&
            this.state.prod.length &&
            this.state.prod.map((prods) => (
              <div className="col-sm-4" key={prods.ID}>
                <div className="card">
                  <div className="card-body">
                    <img
                      className="img"
                      src={"../../img/"+prods.ID+".jpg"}
                      alt="Avatar"
                      width="150px"
                      height="150px"
                    />
                    <h5 className="card-text">
                     <span className="txt"> Product Name </span>:{prods.ProductName}{" "}
                    </h5>
                    <h5 className="card-text">Price: {prods.Price} $</h5>
                    <button className="btn btn-primary" id="btncard"
                      onClick={(event) => this.props.$addItemToCart(prods)}
                    >
                      Add To Cart
                    </button>
                  </div>
                </div>
              </div>
            ))}

          {this.props.$products.map((product) => (
            <h1 key={product}>{product}</h1>
          ))}
        </div>
      </div>
    );
  }
}

const mapStateToProps = (state) => {
  return {
    $products : state.productsReducer.products
  }
}

const mapDispatchToProps = dispatch => {
    return {
      $addItemToCart: (product) => dispatch(add_item_to_shopping_cart(product))

    }
}
export default connect(mapStateToProps, mapDispatchToProps)(Products);